import React, {useState} from 'react'
import {FaGithub, FaTools} from 'react-icons/fa'
import {BiLinkExternal} from "react-icons/bi"

export default function Project({title, subtitle, overview, stack, github, url, ready, mobile}) {
    const [textVisible, setTextVisible] = useState(false)
    const onClickThumbnail = () => {
        if(!textVisible)
            setTextVisible(true)
    }

    const onClickCloseOverview = () =>{
        if(textVisible)
            setTextVisible(false)
    }
    return (
        <div
            className="project"
            onClick={onClickThumbnail}>
                {textVisible
                    ? (<div className={`project__overview ${textVisible? "project__overview--visible" : ""}`}>
                            <div
                                className="btn btn--close project__overview-close-btn"
                                onClick={onClickCloseOverview}>
                                    <span></span>
                                    <span></span>
                            </div>
                            <div className="project__heading">
                                <h3>{title}</h3>
                                <h4>{subtitle}</h4>
                            </div>
                            <p>{overview}</p>
                            <ul className="project__stack">
                                {stack.map((elt,index) => (
                                    <li key={index}>{elt}</li>
                                ))}
                            </ul>
                            {ready
                                ? (<ul className="project__links">
                                    <li className="project__link">
                                        <a href={github} title="Voir code source" target="_blank" rel="noreferrer"><FaGithub /></a>
                                    </li>
                                    <li className={`project__link ${mobile || url === ""? "project__link--hidden" : ""}`}>
                                        <a href={url} title="Accéder au site" target="_blank" rel="noreferrer"><BiLinkExternal /></a>
                                    </li>
                                </ul>)
                                : null
                            }
                       </div>)
                    :(<div id={title} className={`project__thumbnail ${ready? "" : "project__thumbnail--not-ready"} ${mobile? "project__thumbnail--mobile" : ""}`}>
                        {title === "Easybank"
                            ? (<div className="inner"></div>)
                            : ready
                                ? null 
                                : (<FaTools className="project__thumbnail-tool-icon"/>)
                        }
                    </div>)
                }
                <div className="project__title">
                    <h3>{title}</h3>
                </div>
        </div>
    )
}
